import Datepicker from 'vanillajs-datepicker/Datepicker';
import '../../scss/plugins/vanillajs-datepicker.css';
import 'ScssComponents/universal/datepicker.scss';
import { exist } from '../utils';

const datepicker = ({
	selector = '.js-datepicker-element',
} = {}) => {
	const classNames = {
		fieldClass: 'datepicker_field--active_state',
	};
	const datePickers = document.querySelectorAll(selector);
	if (!exist(datePickers)) return;

	datePickers.forEach((datePicker) => {
		const datePickerItem = datePicker;
		const date = new Date();
		const year = date.getFullYear();
		const month = date.getMonth();
		const day = date.getDate();
		datePickerItem.value = `${day}/${month + 1}/${year}`;
		let instance = new Datepicker(datePicker, {
			format: 'dd/mm/yyyy',
		});
		datePickerItem.addEventListener('show', () => {
			datePickerItem.parentNode.classList.add(classNames.fieldClass);
		});
		datePickerItem.addEventListener('hide', () => {
			datePickerItem.parentNode.classList.remove(classNames.fieldClass);
		});
	});
};

export default datepicker;
