	init() {
		const initLayout = layout();
		pageLoad(() => {
			setTimeout(() => {
				window.scrollTo(0, 0);
				document.body.classList.add('body--loaded');
				window.smoother = ScrollSmoother.create({
					content: '.wrapper',
					smooth: 2,
					effects: true,
				});
				ScrollTrigger.refresh();

				if (isTouchDevice()) {
					window.smoother.kill();
				}
			}, 100);
		});

		setTimeout(() => {
			this.importPage();
		}, 0);
	}
