import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';

import { BREAKPOINTS } from '../utils/constants';

gsap.registerPlugin(ScrollTrigger);

const header = () => {
	const CLASSNAMES = {
		bodyOpenMenuState: 'body--open_menu_state',
		submenuOpenState: 'main_menu__item--active_state',
		navMobileMenuOpenState: 'nav__menu_submenu--active_state',
		searchOpenState: 'header_search--active_state',
	};

	const { mediaPoint1 } = BREAKPOINTS;

	let openMenuState = false;

	const $body = document.body;
	const $searchTrigger = document.querySelector('.js-search-trigger');
	const $search = document.querySelector('.js-header-search');
	const $searchInput = $search.querySelector('input');

	const $menuTrigger = document.querySelectorAll('.js-menu-trigger');
	const $submenuTrigger = document.querySelectorAll('.js-submenu-trigger');

	const $navMenuTrigger = document.querySelectorAll('.js-mobile-nav-link');

	// submenu mobile trigger
	ScrollTrigger.matchMedia({
		[`(max-width: ${mediaPoint1 - 1}px)`]: () => {
			$submenuTrigger.forEach(item => {
				item.addEventListener('click', (e) => {
					e.preventDefault();
					const menuItem = item.closest('.main_menu__item');
					menuItem.classList.toggle(CLASSNAMES.submenuOpenState);
				});
			});
		},
	});

	// hamburger
	$menuTrigger.forEach((item) => {
		item.addEventListener('click', () => {
			if (!openMenuState) {
				$body.classList.add(CLASSNAMES.bodyOpenMenuState);
				openMenuState = true;
			} else {
				$body.classList.remove(CLASSNAMES.bodyOpenMenuState);
				openMenuState = false;
			}
		});
	});

	// mobile nav bottom

	$navMenuTrigger.forEach(item => {
		item.addEventListener('click', (e) => {
			e.preventDefault();
			item.closest('.nav__menu_item').querySelector('.nav__menu_submenu').classList.toggle(CLASSNAMES.navMobileMenuOpenState);
		});
	});

	// search
	$searchTrigger.addEventListener('click', (e) => {
		e.preventDefault();
		$search.classList.toggle(CLASSNAMES.searchOpenState);
	});

	document.addEventListener('click', (event) => {
		const outsideClick = !$search.contains(event.target) && !$searchTrigger.contains(event.target);

		if (outsideClick) {
			$search.classList.remove(CLASSNAMES.searchOpenState);
		}
	});

	document.querySelector('.js-search-close').addEventListener('click', (e) => {
		e.preventDefault();
		$search.classList.remove(CLASSNAMES.searchOpenState);
		$searchInput.value = '';
	});
};

export default header;
