import 'ScssComponents/single-section.scss';
import slider from './slider';
import Accordion from './accordion';
import 'ScssComponents/download.scss';

const singleSection = () => {
	const acc = new Accordion({
		triggers: document.querySelectorAll('.js-single-section-trigger'),
		activeStateName: 'single_section--active-mod',
	}).init();
	slider();
};

export default singleSection;
