import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';

import Accordion from './accordion';
import fadeAnim from './fade-anim';
import 'ScssComponents/history-main.scss';
import { BREAKPOINTS } from '../utils/constants';

const historyMain = () => {
	gsap.registerPlugin(ScrollTrigger);
	const { mediaPoint1 } = BREAKPOINTS;
	let initAccordion = false;

	ScrollTrigger.matchMedia({
		[`(max-width: ${mediaPoint1 - 1}px)`]: () => {
			if (!initAccordion) {
				initAccordion = true;
				const acc = new Accordion({
					triggers: document.querySelectorAll('.history_main__year'),
					activeStateName: 'history_main__item_in--active_state',
				}).init();
			}
		},
		[`(min-width: ${mediaPoint1}px)`]: () => {
			fadeAnim();
		},
	});
};

export default historyMain;
