import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';
import { BREAKPOINTS } from '../utils/constants';

gsap.registerPlugin(ScrollTrigger);

const fadeAnim = () => {
	const $fadeItem = document.querySelectorAll('.js-fade-item');
	const { mediaPoint1 } = BREAKPOINTS;

	if (!$fadeItem.length) return;

	const fadeHandler = (scroller) => {
		ScrollTrigger.refresh();
		gsap.set($fadeItem, {
			opacity: 0,
			y: 100,
		});
		$fadeItem.forEach(($item) => {
			ScrollTrigger.create({
				scroller,
				trigger: $item,
				start: 'top 85%',
				// markers: true,
				once: true,
				onEnter: () => {
					gsap.to($item, {
						opacity: 1,
						y: 0,
						duration: 0.6,
						ease: 'none',
					});
				},
			});
		});
	};

	ScrollTrigger.matchMedia({
		[`(min-width: ${mediaPoint1}px)`]: () => {
			fadeHandler(document.body);
		},
		[`(max-width: ${mediaPoint1 - 1}px)`]: () => {
			fadeHandler('.wrapper');
		},
	});
};

export default fadeAnim;
