class Popup {
	constructor(data) {
		this.popup = data.popup;
		this.trigger = data.trigger;
		this.close = data.close;
		this.active = data.active_class;

		this.init = this.init.bind(this);
		this.init();
	}

	openPopup() {
		this.popup.classList.add(this.active);
		document.body.classList.add('body--popup_open');
	}

	closePopup() {
		this.popup.classList.remove(this.active);
		document.body.classList.remove('body--popup_open');
		this.onClose();
	}

	onClose() {
		if (this.currentPlayer) this.currentPlayer.pause();
	}

	init() {
		this.trigger.forEach(item => {
			item.addEventListener('click', (e) => {
				e.preventDefault();
				this.openPopup();
			});
		});
		this.close.forEach(item => {
			item.addEventListener('click', (e) => {
				// e.preventDefault();
				this.closePopup();
			});
		});
		this.popup.addEventListener('mousedown', (e) => {
			// e.preventDefault();
			let target = Array.from(e.target.classList);
			if (target.includes('popup')) {
				this.closePopup();
			}
		});
	}
}

export default Popup;
