import 'ScssComponents/contact-form.scss';
import tomSelect from './tomselect';
import datepicker from './datepicker';

const contactForm = (selector = '.js-contact-form', popupSelector = '.js-popup') => {
	const classNames = {
		bodyActiveClass: 'body--popup_open',
		popupActiveClass: 'popup--active_state',
	};

	// tomSelect();
	datepicker();

	const $forms = document.querySelectorAll(selector);
	const $allPopups = document.querySelectorAll(popupSelector);
	$forms.forEach(($form) => {
		if ($form.hasAttribute('data-form-modal') && $form.getAttribute('data-form-modal') !== '' && $form.getAttribute('data-form-modal') !== null) {
			const popupId = $form.getAttribute('data-form-modal');
			const $popup = document.querySelector(`[data-popup-id=${popupId}]`);

			if ($popup) {
				$form.addEventListener('submit', () => {
					$allPopups.forEach((item) => {
						item.classList.remove(classNames.popupActiveClass);
						document.body.classList.remove(classNames.bodyActiveClass);
						$form.reset();
						$popup.classList.add(classNames.popupActiveClass);
						document.body.classList.add(classNames.bodyActiveClass);
					});
				});
			}
		}
	});
};

export default contactForm;
