import Swiper, { Navigation, Pagination, EffectFade } from 'swiper';
import { exist } from '../utils';
import 'swiper/css';
import 'swiper/css/effect-fade';
import 'ScssComponents/slider.scss';

const slider = ({
	sectionSelector = '.js-slider-section',
	sliderSelector = '.js-slider-slider',
	btnNextSelector = '.js-slider-next-button',
	btnPrevSelector = '.js-slider-prev-button',
	paginationSelector = '.js-slider-pagination',
} = {}) => {
	const $sections = document.querySelectorAll(sectionSelector);
	if (!exist($sections)) return;

	Swiper.use([Navigation, Pagination, EffectFade]);

	$sections.forEach(($section) => {
		const $slider = $section.querySelector(sliderSelector);
		const $btnNext = $section.querySelector(btnNextSelector);
		const $btnPrev = $section.querySelector(btnPrevSelector);
		const $pagination = $section.querySelector(paginationSelector);

		if (!exist([$slider, $btnNext, $btnPrev, $pagination])) return;

		const heroSwiper = new Swiper($slider, {
			speed: 800,
			slidesPerView: 1,
			autoHeight: false,
			effect: 'fade',
			fadeEffect: {
				crossFade: true,
			},
			navigation: {
				nextEl: $btnNext,
				prevEl: $btnPrev,
			},
			pagination: {
				type: 'fraction',
				el: $pagination,
			},
		});
	});
};

export default slider;
