import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';
import { BREAKPOINTS } from '../utils/constants';

const fixedBtnTrigger = () => {
	const { mediaPoint1 } = BREAKPOINTS;

	const $triggers = document.querySelectorAll('.js-static-email-btn');
	const $btn = document.querySelector('.js-fixed-email-btn');

	const hideBtn = () => {
		$btn.classList.add('fixed_btn--hidden_mod');
	};

	const showBtn = () => {
		$btn.classList.remove('fixed_btn--hidden_mod');
	};

	const initHandler = (scroller) => {
		ScrollTrigger.refresh();
		$triggers.forEach((item) => {
			const $trigger = item.closest('.section');
			ScrollTrigger.create({
				scroller,
				trigger: $trigger,
				// markers: true,
				onEnter: hideBtn,
				onLeave: showBtn,
				onEnterBack: hideBtn,
				onLeaveBack: showBtn,
			});
		});
	};

	ScrollTrigger.matchMedia({
		[`(min-width: ${mediaPoint1}px)`]: () => {
			initHandler(document.body);
		},
		[`(max-width: ${mediaPoint1 - 1}px)`]: () => {
			initHandler('.wrapper');
		},
	});
};

export default fixedBtnTrigger;
