import Swiper, { Navigation, Pagination } from 'swiper';
import 'swiper/css';
import 'swiper/scss/scrollbar';
import { buildSwiper, removeSwiper } from './buildSwiper';

import 'ScssComponents/section-heading.scss';
import 'ScssComponents/products.scss';

import mobileSlider from './mobile-slider';

Swiper.use([Navigation]);

const products = () => {
	const sliderClass = '.productsSlider';
	const $slider = document.querySelectorAll(sliderClass);
	mobileSlider({
		selector: '.js-products-slider',
		controls: true,
	});

	if ($slider.length) {
		$slider.forEach(item => {
			const $prev = item.closest('.products__list_wrap').querySelector('.js-products-prev');
			const $next = item.closest('.products__list_wrap').querySelector('.js-products-next');

			buildSwiper(item);

			let sliderEl = new Swiper(item, {
				observer: true,
				observeParents: true,
				speed: 800,
				slidesPerView: 'auto',
				navigation: {
					prevEl: $prev,
					nextEl: $next,
				},
			});
		});
	}
};

export default products;
