import Accordion from './accordion';
import 'ScssComponents/hero-press-area.scss';
import 'ScssComponents/contact-form.scss';
import 'ScssComponents/form-section.scss';

const pressAreaHero = () => {
	const acc = new Accordion({
		triggers: document.querySelectorAll('.press_area_hero__accordion_item_head'),
		activeStateName: 'press_area_hero__accordion--active_mod',
	}).init();
};

export default pressAreaHero;
