import { onWindowResize, exist } from '../utils/index';

const footer = () => {
	const selectors = {
		select: '.js-footer-select',
		title: '.js-footer-title',
		menu: '.js-footer-menu',
		selectOpen: 'footer_select--open_state',
	};

	const $selects = document.querySelectorAll(selectors.select);

	if (!exist($selects)) return;

	$selects.forEach(($select) => {
		const $title = $select.querySelector(selectors.title);
		const $menu = $select.querySelector(selectors.menu);

		$title.addEventListener('click', () => {
			if ($select.classList.contains(selectors.selectOpen)) {
				$menu.style.maxHeight = null;
				$select.classList.remove(selectors.selectOpen);
			} else {
				$select.classList.add(selectors.selectOpen);
				$menu.style.maxHeight = `${$menu.scrollHeight * 0.1}rem`;
			}
		});
	});
};

export default footer;
