import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';
import { BREAKPOINTS } from '../utils/constants';

gsap.registerPlugin(ScrollTrigger);

const animFade = () => {
	const { mediaPoint1 } = BREAKPOINTS;

	const $fadeEl = document.querySelectorAll('.js-fade-el');

	ScrollTrigger.matchMedia({
		[`(min-width: ${mediaPoint1}px)`]: () => {
			$fadeEl.forEach((item) => {
				const $img = item.querySelector('.js-scale-el');
				gsap.set(item, {
					opacity: 0,
				});

				gsap.set($img, {
					scaleX: 1.15,
					scaleY: 1.15,
				});

				ScrollTrigger.create({
					trigger: item,
					start: 'top 80%',
					//	markers: true,
					onEnter: () => {
						gsap.to(item, {
							opacity: 1,
							duration: 1,
							ease: 'none',
						});
						if ($img) {
							gsap.to($img, {
								scaleX: 1,
								scaleY: 1,
								duration: 0.6,
								ease: 'none',
							});
						}
					},
				});
			});
		},
	});
};

export default animFade;
