import Popup from './popup';
import 'ScssComponents/popup.scss';

const popupBlock = () => {
	const $popups = document.querySelectorAll('.js-popup');

	if (!$popups.length) return;

	$popups.forEach(($popup) => {
		const classNames = {
			readyClass: 'popup--ready_state',
			activeClass: 'popup--active_state',
		};
		const $triggers = document.querySelectorAll(`[data-popup-trigger='${$popup.dataset.popupId}']`);
		const $close = $popup.querySelectorAll('.js-popup-close');
		setTimeout(() => {
			$popup.classList.add(classNames.readyClass);
		});

		const popup = new Popup({
			trigger: $triggers,
			popup: $popup,
			close: $close,
			active_class: classNames.activeClass,
		});
	});
};

export default popupBlock;
