const clearForm = () => {
	const $trigger = document.querySelectorAll('.js-clear-input');

	// console.log('123');

	$trigger.forEach((item) => {
		item.addEventListener('click', (e) => {
			e.preventDefault();
			const $input = item.closest('form').querySelector('input');
			$input.value = '';
		});
	});
};

export default clearForm();
