import mobileSlider from './mobile-slider';
import Accordion from './accordion';
import 'ScssComponents/download-area-section.scss';

const downloadAreaSec = () => {
	const acc = new Accordion({
		triggers: document.querySelectorAll('.download_area__accordion_item_head'),
		activeStateName: 'download_area__accordion--active_mod',
	}).init();

	mobileSlider({
		selector: '.js-download-area-slider',
		controls: true,
	});
};

export default downloadAreaSec;
